<template>
  <section class="page-demo">
    <ptheader class="rpublicity_header">医德医风档案信息</ptheader>
    <div>
      <ul>

        <li>
          <span>建档时间</span>
          <span><input type="text" v-model="recordCreateTime" @click="setDate1"/></span>
        </li>
        <li>
          <span>机构名称</span>
          <span><input type="text" v-model="unitRootName" readonly/></span>
        </li>
        <li>
          <span>所属科室</span>
          <span><input type="text" v-model="unitName" readonly/></span>
        </li>
        <li>
          <span>姓名</span>
          <span><input type="text" v-model="name" readonly/></span>
        </li>
        <li>
          <span>性别</span>
          <span>
              <input type="hidden" v-model="sex"/>
              <input type="text" v-model="sexValue" placeholder="请选择" @click="sexType" readonly/>
          </span>
        </li>


        <mt-popup class="sharePopup" v-model="sexValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="sexValueShow=!sexValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in sexTypeArray" @click="chooseSex(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>民族</span>
          <span>
                <input type="hidden" v-model="nation"/>
                <input type="text" v-model="nationValue" placeholder="请选择" @click="nationType()"/>
            </span>
        </li>

        <mt-popup class="sharePopup" v-model="nationValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="nationValueShow=!nationValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in nationTypeArray" @click="chooseNation(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>党派</span>
          <span>
            <input type="hidden" v-model="partisan"/>
            <input type="text" v-model="partisanValue" placeholder="请选择" @click="partisanType()"/>
          </span>
        </li>
        <mt-popup class="sharePopup" v-model="partisanValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="partisanValueShow=!partisanValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in partisanArray" @click="choosePartisan(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>出生年月</span>
          <span><input type="text" v-model="birthday" @click="setDate2()"/></span>
        </li>


        <li>
          <span>职称</span>
          <span>
              <input type="hidden" v-model="jobRank"/>
              <input type="text" v-model="jobRankValue" placeholder="请选择" @click="jobRankType()"/>
          </span>
        </li>
        <mt-popup class="sharePopup" v-model="jobRankValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="jobRankValueShow=!jobRankValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in jobRankArray" @click="chooseJobRank(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>职务</span>
          <span>
              <input type="hidden" v-model="jobDuties"/>
              <input type="text" v-model="jobDutiesValue" placeholder="请选择" @click="jobDutiesType()"/>
          </span>
        </li>
        <mt-popup class="sharePopup" v-model="jobDutiesShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="jobDutiesShow=!jobDutiesShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in jobDutiesArray" @click="chooseJobDuties(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>学历</span>
          <span>
              <input type="hidden" v-model="oeducation"/>
              <input type="text" v-model="oeducationValue" placeholder="请选择" @click="oeducationType()"/>
          </span>
        </li>
        <mt-popup class="sharePopup" v-model="oeducationValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="oeducationValueShow=!oeducationValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in oeducationArray" @click="chooseOeducation(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>参加工作时间</span>
          <span><input type="text" v-model="startWorkTime" @click="setDate3()"/></span>
        </li>


        <li>
          <span>进现单位工作时间</span>
          <span><input type="text" v-model="comeUnitTime" @click="setDate4()"/></span>
        </li>


        <li>
          <span>是否考评人员</span>
          <span>
              <input type="hidden" v-model="isAssessor"/>
              <input type="text" v-model="isAssessorValue" placeholder="请选择" @click="isAssessorers()" readonly/>
          </span>
        </li>


        <mt-popup class="sharePopup" v-model="isAssessorValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="isAssessorValueShow=!isAssessorValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in isAssessorArray" @click="chooseisAssessor(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>人员属性</span>
          <span>
              <input type="hidden" v-model="userProperty"/>
              <input type="text" v-model="userPropertyValue" placeholder="请选择" @click="userPropertyType()"/>
          </span>
        </li>
        <mt-popup class="sharePopup" v-model="userPropertyValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="userPropertyValueShow=!userPropertyValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in userPropertyArray" @click="chooseUserProperty(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>职级</span>
          <span>
              <input type="hidden" v-model="jobLevel"/>
              <input type="text" v-model="jobLevelValue" placeholder="请选择" @click="jobLevelType()"/>
          </span>
        </li>


        <mt-popup class="sharePopup" v-model="jobLevelValueShow" position="bottom">
          <div class="shareFooter">
            <div class="shareCancel" @click="jobLevelValueShow=!jobLevelValueShow">取消</div>
          </div>
          <ul class="shareUl">
            <li v-for="item in jobLevelArray" @click="chooseJobLevel(item.key,item.value)">{{item.value}}</li>
          </ul>
        </mt-popup>


        <li>
          <span>身份证号</span>
          <span><input type="text" v-model="identification" readonly/></span>
        </li>
      </ul>
    </div>
    <footer @click="submit">
      提交
    </footer>
  </section>
</template>
<script>


  import ptheader from "@/components/header/ptheader";
  import {Toast} from 'mint-ui';

  export default {
    data() {
      return {
        recordCreateTime: '',
        name: '',
        sex: '',
        nation: '',
        partisan: '',
        birthday: '',
        jobRank: '',
        jobDuties: '5543543543',
        oeducation: '',
        startWorkTime: '',
        comeUnitTime: '',
        isAssessor: '',
        userProperty: '',
        jobLevel: '',
        identification: '',
        sexTypeArray: [],
        sexValue: '',
        sexValueShow: false,
        nationValue: '',
        nationTypeArray: [],
        nationValueShow: false,
        partisanValue: '',//党派
        partisanArray: [],
        partisanValueShow: false,
        jobDutiesValue: '',//职务
        jobDutiesShow: false,
        jobDutiesArray: [],
        jobRankValue: '',//职称
        jobRankValueShow: false,
        jobRankArray: [],
        oeducationValue: '',//学历
        oeducationValueShow: false,
        oeducationArray: [],
        jobLevelValue: '',//职级
        jobLevelArray: '',//职级
        jobLevelValueShow: false,//职级
        userPropertyValue: '',//属性
        userPropertyValueShow: false,//属性
        userPropertyArray: [],//属性
        isAssessorValue: '',//是否考评人员
        isAssessorValueShow: false,//是否考评人员
        isAssessorArray: [],//
        unitRootName: '',//所属科室
        unitName: '',//机构名称
        dataValues: '',
        constants: ''

      };
    },
    components: {
      ptheader
    },
    methods: {
      setDate1() {
        this.$calendar.show({
          onOk: (date) => {
            this.recordCreateTime = date;
          }
        });
      },

      setDate2() {
        this.$calendar.show({
          onOk: (date) => {
            this.birthday = date;
          }
        });
      },
      setDate3() {
        this.$calendar.show({
          onOk: (date) => {
            this.startWorkTime = date;
          }
        });
      },
      setDate4() {
        this.$calendar.show({
          onOk: (date) => {
            this.comeUnitTime = date;
          }
        });
      },
      sexType() {
        this.sexValueShow = true;
      },
      chooseSex(key, value) {
        this.sex = key;
        this.sexValue = value;
        this.sexValueShow = false;
      },
      nationType() {
        this.nationValueShow = true;
      },
      chooseNation(key, value) {
        this.nation = key;
        this.nationValue = value;
        this.nationValueShow = false;
      },
      partisanType() {
        this.partisanValueShow = true;
      },
      choosePartisan(key, value) {
        this.partisan = key;
        this.partisanValue = value;
        this.partisanValueShow = false;
      },
      jobDutiesType() {
        this.jobDutiesShow = true;
      },
      chooseJobDuties(key, value) {
        this.jobDuties = key;
        this.jobDutiesValue = value;
        this.jobDutiesShow = false;
      },
      jobRankType() {
        this.jobRankValueShow = true;
      },
      chooseJobRank(key, value) {
        this.jobRank = key;
        this.jobRankValue = value;
        this.jobRankValueShow = false;
      },
      jobRankType() {
        this.jobRankValueShow = true;
      },
      chooseJobRank(key, value) {
        this.jobRank = key;
        this.jobRankValue = value;
        this.jobRankValueShow = false;
      },
      oeducationType() {
        this.oeducationValueShow = true;
      },
      chooseOeducation(key, value) {
        this.oeducation = key;
        this.oeducationValue = value;
        this.oeducationValueShow = false;
      },
      jobLevelType() {
        this.jobLevelValueShow = true;
      },
      chooseJobLevel(key, value) {
        this.jobLevel = key;
        this.jobLevelValue = value;
        this.jobLevelValueShow = false;
      },
      userPropertyType() {
        this.userPropertyValueShow = true;
      },
      chooseUserProperty(key, value) {
        this.userProperty = key;
        this.userPropertyValue = value;
        this.userPropertyValueShow = false;
      },
      isAssessorers() {
        this.isAssessorValueShow = true;
      },
      chooseisAssessor(key, value) {
        this.isAssessor = key;
        this.isAssessorValue = value;
        this.isAssessorValueShow = false;
      },
      changetime(e) {
        var newTime = new Date(e);
        var m = newTime.getMonth() + 1;
        var y = newTime.getFullYear();
        var d = newTime.getDate();
        if (m < 10 && d < 10) {
          return y + "-0" + m + "-0" + d;
        }
        if (m < 10 & d > 9) {
          return y + "-0" + m + "-" + d;
        }
        if (m > 9 & d < 10) {
          return y + "-" + m + "-0" + d;
        }
        else {
          return y + "-" + m + "-" + d;
        }

      },
      init() {
        let _this = this;
        let params = new URLSearchParams();
        params.append('code', 'sex-type,nation-type,partisan-type,job-duties-type,job-rank-type,job_level,oeducation-type,user_property_type,boolean-type');
        this.$http({
          method: 'post',
          url: '/common/constant',
          data: params
        }).then(function (res) {

          _this.sexTypeArray = res['sex-type'];
          _this.nationTypeArray = res['nation-type'];
          _this.partisanArray = res['partisan-type'];
          _this.jobDutiesArray = res['job-duties-type'];
          _this.jobRankArray = res['job-rank-type'];//职称
          _this.oeducationArray = res['oeducation-type'];//职称
          _this.jobLevelArray = res['job_level'];//职级
          _this.userPropertyArray = res['user_property_type'];//属性
          _this.isAssessorArray = res['boolean-type'];//是否考评人员
          _this.constant = res;
        });
      },
      submit() {
        //alert('是否考评任玉环'+this.isAssessor);
        let _this = this;
        let userId = JSON.parse(localStorage.getItem("ccc")).userId;
        var param = new FormData();
        param.append('userId', userId);
        param.append('recordCreateTime', this.recordCreateTime || '');
        param.append('unitRootName', this.unitRootName || '');
        param.append('unitName', this.unitName || '');
        param.append('name', this.name || '');
        param.append('sex', this.sex);
        param.append('nation', this.nation || '');
        param.append('partisan', this.partisan || '');
        param.append('birthday', this.birthday || '');
        param.append('jobRank', this.jobRank || '');
        param.append('jobDuties', this.jobDuties || '');
        param.append('oeducation', this.oeducation || '');
        param.append('startWorkTime', this.startWorkTime || '');
        param.append('comeUnitTime', this.comeUnitTime || '');
        param.append('isAssessor', this.isAssessor || '');
        param.append('userProperty', this.userProperty || '');
        param.append('jobLevel', this.jobLevel || '');
        param.append('identification', this.identification || '');

        this.$http({
          method: 'post',
          url: '/org/user/self/app/update',
          data: param
        }).then(res => {
          Toast("保存成功");
          _this.$router.push('/accounts/index');
        });


      },
      getData() {
        let _this = this;
        if (this.dataValues && this.constant) {
          let userId = JSON.parse(localStorage.getItem("ccc")).userId;
          this.$http.get('/org/user/self/get?id=' + userId).then(res => {


            _this.recordCreateTime = _this.changetime(res.recordCreateTime);
            _this.unitRootName =res.unitRootName;
            _this.unitName =res.unitName;
            _this.name = res.name;


            //性别
            _this.sex = res.sex;
            _this.sexTypeArray.forEach(function(data){
              if(res.sex ==data.key) {
                _this.sexValue = data.value;
              }
            });


            //民族
            _this.nation = res.nation;
            _this.nationTypeArray.forEach(function(data){
              if(res.nation ==data.key) {
                _this.nationValue = data.value;
              }
            });


            //党派
            _this.partisan = res.partisan;
            _this.partisanArray.forEach(function(data){
              if(res.partisan ==data.key) {
                _this.partisanValue = data.value;
              }
            });
            //职务
            _this.jobDuties = res.jobDuties;
            _this.jobDutiesArray.forEach(function(data){
              if(res.jobDuties ==data.key) {
                _this.jobDutiesValue = data.value;
              }
            });

            //职称
            _this.jobRank = res.jobRank;
            _this.jobRankArray.forEach(function(data){
              if(res.jobRank ==data.key) {
                _this.jobRankValue= data.value;
              }
            });
            // 学历
            _this.oeducation = res.oeducation;
            _this.oeducationArray.forEach(function(data){
              if(res.oeducation ==data.key) {
                _this.oeducationValue= data.value;
              }
            });
            //职级
            _this.jobLevel = res.jobLevel;
            _this.jobLevelArray.forEach(function(data){
              if(res.jobLevel ==data.key) {
                _this.jobLevelValue= data.value;
              }
            });
            //属性
            _this.userProperty = res.userProperty;
            _this.userPropertyArray.forEach(function(data){
              if(res.userProperty ==data.key) {
                _this.userPropertyValue= data.value;
              }
            });

            //是否考评人员
            _this.isAssessor = res.isAssessor;
            _this.isAssessorArray.forEach(function(data){
              if(res.isAssessor ==data.key) {
                _this.isAssessorValue= data.value;
              }
            });
            _this.birthday = _this.changetime(res.birthday);
            _this.startWorkTime =  _this.changetime(res.startWorkTime);
            _this.comeUnitTime =  _this.changetime(res.comeUnitTime);
            _this.assessUnitId = res.assessUnitId;
            _this.identification = res.identification;



          });

        }

      }
    },

    created() {
      this.init();


      let _this = this;
      let userId = JSON.parse(localStorage.getItem("ccc")).userId;
      this.$http.get('/org/user/self/get?id=' + userId)
        .then(res => {
          _this.dataValues = res;

          _this.getData();

        });
    }
  };
</script>
<style scoped>
  ul li {
    height: 4rem;
    text-indent: 1rem;
    line-height: 4rem;
    border-bottom: 1px solid #f7f7f7;

  }

  ul li input {
    width: 14rem;
    height: 3rem;
  }

  .workHours {
    width: 14rem;
  }

  .workingHours {
    width: 14rem;
  }

  li span:nth-child(2n) {
    float: right;
    text-align: left;
    margin-right: 1rem;
  }

  li span:nth-child(2n) input {
    text-align: left;
  }

  footer {
    width: 100%;
    height: 3rem;
    color: white;
    letter-spacing: 0.5rem;
    background: #0b752b;
    line-height: 3rem;
    text-align: center;
    position: fixed;
    bottom: 0px;
    font-size: 16px;
  }

  .mint-popup {
    width: 100%;
  }

  .mint-popup ul li {
    text-indent: 2vw;
  }

  .shareFooter {
    width: 100%;
    text-align: right;
    padding-right: 2vw;
    line-height: 3rem;
  }

  .shareUl {
    height: 400px;
    overflow-y: scroll;
  }

  .shareCancel {
    height: 3rem;
    width: 100%;
    padding-right: 1vw;
  }
</style>



